.so__groups__sticky{
  position: fixed;
  right: 0;
  z-index: 999;
  background-color: $bgColor;
  box-shadow: -2px 0 8px 0 #ddd;
  width: 50px;
  border-radius: 4px 0 0 4px;
  top: 196px;
}
.dark__mode {background-color: $bgColorDark;}
.light__mode {background-color: $bgColorLight;}
.switch-checkbox {
  padding: 10px;
  cursor: pointer;
}
