@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');
@import './styles/config';

body {margin: auto;padding: 0px;overflow-x: hidden;background-repeat: repeat;font-family: $font;}
.clearfix{clear:both;}
img {max-width: 100%;height: auto;}
p{line-height: 1.8;color: $textColor;font-family: $font;}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {color: $headingColor;font-family: $font;}
a{ font-family: $font;  text-decoration: none;}
li{font-family: $font;}
dl, ol, ul { padding: 0;}
* {word-break: break-word;}
*, *::before, *::after {box-sizing: border-box;}
.align__center{text-align: center;}
// search
.loc__searsss{
  width: 100%;
  height: 62px;
  margin: 77px auto;
  .loc__searsss__two {
    background-color: #fff;
    width: 100%;
    float: left;
    box-shadow: 1px 20px 20px 0px #d0d1d245;
    padding: 17px;
    outline: 7px solid #ffffff7a;
    outline-offset: 0px;
    .searchform__btn {
      background-color: $primaryColor;
      text-align: center;
      padding: 10px 17px 10px 17px;
      cursor: pointer;
      display: table;
      margin: 0 auto;
      border-radius: 7px;
      svg{
        color: white;
        font-size: 27px;
        margin-right: 4px;
        vertical-align: middle;
      }
  }
    .loc__searsss__btn {
      color: #ffffff;
      margin-bottom: 0px;
      background: transparent;
      font-size: 23px;
      font-weight: 600;
      font-family: "Inter", sans-serif;
      cursor: pointer;
    }
  }
  .locationsss {
    padding: 10px 10px 10px 10px;
    border: 1px solid #e1e1e1;
    width: 100%;
    float: left;
    svg{
      color: $primaryColor;
      font-size: 24px;
      vertical-align: middle;
    }
    .form-control-mapsqqq {
      display: inline-block;
      width: 93%;
      input {
        border: 0px solid #eee;
        outline: none;
        margin: 0;
        padding: 3px 0 4px 7px;
      }
    }
  }
  .loc__searsss__types{
    position: relative;
    cursor: pointer;
    padding: 10px 10px 10px 10px;
    border: 1px solid #e1e1e1;
    svg{
      float: right;
      vertical-align: middle;
      margin-top: 6px;
    }
    p{
      display: -webkit-box;
    max-width: 100%;
    max-height: 27px;
    margin: 0 auto;
    text-align: left;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    }
    .loc__searsss__types__two.budgetsss{
      min-width: 300px;
      li{
        margin: 0 0px 10px 0px;
      }
    }
      
    .loc__searsss__types__two{
      position: absolute;
      top: 63px;
      left: 0;
      border-radius: 4px;
      background-color: #fff;
      min-width: 277px;
      padding: 27px 27px 17px;
      box-shadow: 0px 3px 10px #bebfbf;
      z-index: 999;
      max-height: 377px;
      overflow-x: hidden;
      overflow-y: auto;
      &::before {
        content: "";
        position: absolute;
        left: 22%;
        top: -7px;
        width: 14px;
        height: 14px;
        background-color: #ffffff;
        margin-left: -7px;
        transform: rotate(45deg);
        z-index: 10;
        cursor: auto;
      }
      h4{
        font-size: 16px;
        font-weight: 500;
        margin-bottom: 17px;
      }
      li {
        list-style-type: none;
        display: inline-block;
        float: left;
        margin: 0 7px 17px 7px;
        border: 1px solid #00aeef;
        padding: 6px 10px 6px 10px;
        border-radius: 7%;
        cursor: pointer;
        
      }
      li.activeresss{
        background-color: #fbe9e9;
        border: 1px solid $primaryColor;
      }
    }
  }
}
// --------------------------------------
.sec1{
  position: relative;
  clear: both;
  display: block;
  width: 100%;
  float: left;
  margin-top: 0;
  padding: 60px 0;
}
.sec2{
  position: relative;
  clear: both;
  display: block;
  width: 100%;
  float: left;
  margin-top: 0;
  padding: 60px 0;
}
.buy__frontsss{
  border-radius: 5px;
  position: relative;
  overflow: hidden;
  background: #ffffff;
  padding: 17px 40px;
  transition: all 0.5s ease-in-out 0s;
  margin-bottom: 37px !important;
  h2 {
    text-align: center;
    margin: 27px 0 27px 0px;
    clear: both;
    float: left;
    width: 100%;
    span {
      font-size: 14px;
      font-weight: 400;
      color: #696868;
      text-align: center;
      width: 100%;
      display: block;
      margin-top: 7px;
    }
  }
  
}
.buy__frontsss2 .img__1 { 
  height: 237px;
  overflow: hidden;
  img{ 
    .buy__frontsss2 .img__1 img {
      height: 237px;
      margin: 0 auto 27px;
      display: block;
      object-fit: cover;
      object-position: center top;
      width: 100%;
      border-top-left-radius: 3%;
      border-top-right-radius: 4%;
  }
    }
}
.btn2{margin-left: 37px;}
//---------------------------------------------------------
.homesss {
  position: relative;
  width: 100%;
  float: left;
  height: 477px;
  background-size: cover;
  background-repeat: no-repeat;
  .menu-item a {
    color: #ffffff;
  }
  h1 {
    text-align: center;
    padding-top: 7%;
    font-size: 37px;
    margin-bottom: 17px;
    color: white;
    span{
      color: #ffc107;
    }
  }
  h6 {
    text-align: center;
    font-size: 23px;
    color: white;
    font-weight: 400;
  }
}
.search__pages{
  .loc__searsss {
    height: auto;
    margin: 0 auto;
    background-color: $primaryColor;
    float: left;
    .loc__searsss__two {
      background-color: $primaryColor;
      outline: 0px solid #ffffff7a;
    }
    .loc__searsss__types {
      background-color: white;
      p{
        color:rgb(0, 0, 0);
      }
    }
    .locationsss{
      background-color: white;
    }
    .searchform__btn {
      background-color: #000000;
      padding: 9px 17px 9px 17px;
    }
  }
}