 input {
  width: 100%;
  padding: 0.7rem 1rem;
  border: 1px solid #eee;
  outline: none;
  border-radius: 5px;
  display: block;
  margin-bottom: 0.5rem;
}
select {
  width: 100%;
  padding: 0.7rem 1rem;
  border: 1px solid #eee;
  outline: none;
  border-radius: 5px;
  display: block;
  margin-bottom: 0.5rem;
  font-family: 'Inter', sans-serif;
}
textarea {
  width: 100%;
  padding: 0.7rem 1rem;
  border: 1px solid #eee;
  outline: none;
  border-radius: 5px;
  display: block;
  margin-bottom: 0.5rem;
}
label{
  margin-bottom: 10px;
  display: block;
  font-size: 16px;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
}
.Toastify__toast--success {
  background: #00bf6f!important;
}
button[type="submit"]:hover {
  color: #fff;
  background: #00bf6f;
  border-color: #00bf6f;
}
button[type="submit"]{
  text-transform: uppercase;
    height: 35px;
    line-height: 35px;
    padding: 0 15px;
    font-weight: normal;
    background: #000;
    text-decoration: none;
    border: 1px solid #525252;
    border-radius: 0;
    vertical-align: top;
    color: #fff;
    cursor: pointer;
    font-family: 'Inter', sans-serif;
}
.required {
  color: red;
  font-weight: 700;
  border: 0!important;
  text-decoration: none;
}
.radio, .checkbox {
  display: block;
  min-height: 20px;
  padding-left: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  vertical-align: middle;
}
 input[type="radio"], .radio-inline input[type="radio"], .checkbox input[type="checkbox"], .checkbox-inline input[type="checkbox"] {
  display: inline-block;
  width: auto;
}
input[type=checkbox] {
  zoom: 1.35;
  margin-top: 2px;
  vertical-align: middle;
}
input[type="text"], input[type="tel"], input[type="password"], input[type="email"], input[type="url"], input[type="date"], input[type="number"], textarea, select{
  background-color: #FAFBFC;
  border: 1px solid #E9E9EE;
  padding: 0 20px;
  max-width: 100%;
  border-radius: 4px;
  line-height: 37px;
  color: #717580;
  font-size: 15px;
}
.mr-10{margin-top: 10px;}
.Toastify__toast-container {
  width: auto!important;
}
.Toastify__toast-body {
  padding: 10px 17px 20px 17px;
  line-height: 27px;
  font-size: 18px;
  font-family: 'Inter', sans-serif;
  text-transform: capitalize;
}
.Toastify__toast--default {
  font-family: 'Inter', sans-serif;
  color: #6b6c6d!important;
}
.btn {
  font-size: 18px;
  padding: 7px 17px 7px 17px;
  border-radius: 3px;
}
.btn-primary {
  background-color: #7366ff !important;
  border-color: #7366ff !important;
  color:white;
  display: inline-block;
  margin-top: 10px;
  margin-bottom: 17px;
  cursor: pointer;
}
